import React from "react";
import PropTypes from "prop-types";
import "./Movie.css";

function Movie({ id, title, year, rating, summary, poster, genres }) {
  return (
    <div id={id} className="movie">
      <img className="img" src={poster} alt={title}></img>
      <div className="movie_data">
        <h3 className="title">{title}</h3>
        <div className="movie_data_ele">
          <h5 className="year">{year}</h5>
          <h5 className="rating">{rating}</h5>
        </div>
        <ul className="genres">
          {genres.map((genre, index) => (
            <li key={index}>{genre}</li>
          ))}
        </ul>
        <p className="summary">{summary.slice(0, 130)}...</p>
      </div>
    </div>
  );
}

Movie.prototype = {
  id: PropTypes.number.isRequired,
  title: PropTypes.string.isRequired,
  year: PropTypes.number.isRequired,
  rating: PropTypes.number.isRequired,
  summary: PropTypes.string.isRequired,
  poster: PropTypes.string.isRequired,
  genres: PropTypes.arrayOf(PropTypes.string).isRequired
};

export default Movie;
